.product{
	display: flex;
	flex-direction: column;
	z-index: 1;
	background-color: white;
	align-items: center;
	justify-content: flex-end;
	margin: 10px;
	padding: 20px;
	width: 100%;
	min-width: 100px;
	max-height: 400px;


}

.product > img{
	max-height: 200px;
	width: 100%;
	object-fit: contain;
	margin-bottom: 15px;
}

.product > button{
	background : #f0c14b;
	margin-top: 10px;
	border: 1px solid;
	border-color: #a88734 #9c7e31 #846a29;
	color: #111;
}

.product_price{
	margin-top: 5px;
}

.product_rating{
	display: flex;
}

.product_info{
	height: 100px;
	margin-bottom: 15px;

}