.checkoutProduct{
	display: flex;
	margin-bottom: 20px;
	margin-top: 20px;
}

.checkout_info{
	padding-left: 20px;
}

.checkout_info > button{
	background : #f0c14b;
	margin-top: 10px;
	border: 1px solid;
	border-color: #a88734 #9c7e31 #846a29;
	color: #111;
}

.checkout_image{
	object-fit: contain;
	width: 180px;
	height: 180px;
}

.checkout_product_rating{
	display: flex;
}

checkout_title{
	font-size: 17px;
	font-weight: 800;
}