.header{
	height: 60px;
	display: flex;
	align-items: center;
	background-color:#131921;
	position: sticky;
	top: 0;
	z-index: 100; 
}

.header_logo{
	height: 55px;
	width: 100px;
	object-fit: contain;
	margin: 0 20px;
	top: 0;
}

.header__search{
	display: flex;
	flex: 1;
	align-items: center;
	border-radius: 24px;
}

.header_search_Input{
	height: 12px;
	padding: 10px;
	border: none;
	width: 100%;
}



.header_searchicon{
	padding: 5px;
	height: 22px !important;
	background-color: #cd9042;

}

.header__optionOne{
	font-size: 10px;
}

.header__optionTwo{
	font-size: 13px;
	font-weight: 800;
}

.header_optionbasket{
	display: flex;
	align-items: center;
	color: white;
}
.header_basketcount{
	margin-left: 10px;
	margin-right: 10px;
}
.header_nav{
	display: flex;
	justify-content: space-evenly;
}




.header_option{
	display: flex;
	flex-direction: column;
	margin-left: 10px;
	margin-right: 10px;
	color: white; 
}